.container {
    max-width: 1220px;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}


.need_sec { 

    .container{
        .tab{
            ul {
                .nav { 
                    .nav-tabs{
                        padding: 25px 0px;
                        flex-wrap: initial;

                    }
                }
            }
        }
    }
}

.tab-nav-center {
    
    .nav {
        justify-content: center;
    }
}

.nav-tabs {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #ebebeb;
}

.nav-link-with-img {
    position: relative;
    margin-bottom: 0px !important;
    height: 100%;
    padding: 10px;
    border: 1px solid #22222236;
    border-radius: 100px !important;
    background: #e1e1e1;
    margin-right: 10px;
}
.nav-link {
    display: block;
    margin-bottom: -0.1rem;    
    line-height: 1;
    letter-spacing: 0;
    text-align: center;
    border-radius: 0;
    border: 0;
    transition: border 0.35s, color 0.35s, background-color 0.35s;
}
.nav-link-with-img .img-cat-title {
    /* position: absolute; */
    bottom: 1.5rem;
    font-size: 14px;
    font-weight: 400;
    min-width: 12.1rem;
    font-family:var(--font-monda);
}